.about {
    padding-top: 80px;
    padding-bottom: 100px;
    display: flex;
    justify-content: center;

    // .about__image

    &__image {
        width: 25%;
        margin-right: 10%;
    }

    // .about__content

    &__content {
        width: 36%;
    }

    // .about__title

    &__title {
        margin-top: 15px;
        margin-bottom: 23px;
        color: $color-dark-l;
    }

    // .about__text
    
    &__text {
        color:$color-dark-xl;
    }
    // .about__text
    
    &__text:not(last-child) {
        margin-bottom: 25px;
    }
}