.pets {
    padding-top: 120px;
    background-color: $color-light-l;
    

    // .pets__container

    &__container {
        padding-top: 80px;
        padding-bottom: 100px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    // .pets__title

    &__title {
        margin-top: 0;
        margin-bottom: 45px;
        text-align: center;
        color:$color-dark-l;
    }

    // .pets__cards

    &__cards {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 15px 0;
        margin-bottom: 20px;
    }

    // .pets__card

    &__card {
        margin-bottom: 30px;
    }


    // .pets__pagination

