.friends {
    padding-top:80px;
    padding-bottom: 100px;
    background-color: $color-light-l;

    // .friends__container

    &__container {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    // .friends__slider

    &__slider {
        width: 100%;
        margin-bottom: 60px;
    }

    // .ftiends__title
    
    &__title {
        text-align: center;
        margin-top: 0;
        margin-bottom: 60px;
        color:$color-dark-l;
    }
}

.slider {
    display: flex;
    align-items: center;
    justify-content: space-between;

    // .slider__content
    
    &__content {
        display: flex;
        width: 100%;
        justify-content: space-around;
    }

