.footer {
    padding-top: 28px;

    background: $start-screen-gradient-background;

    // .footer__container

    &__container {
        display: grid;
        grid-template-columns: 1fr 1fr 320px;
    }

    // .footer__contacts

    &__contacts {
        & .footer-list{
            margin-top: 40px;
        }
        
    }

    // .footer__title

    &__title {
        color:$color-light-xl;
    }

    // .footer__adress

    // &__adress {}

    // .footer__image

    &__image {
        justify-self: end;
        align-self: flex-end;
        margin-top: 10px;
    }
}

.footer-list {
    list-style-type: none;
    padding: 0;
    margin: 0;

    // .footer-list__item

    &__item:not(:last-child) {
        margin-bottom: 40px;
    }

    // .footer-list__link

    &__link {
        color:$color-primary;
        text-decoration: none;
        display: inline-flex;
        align-items: center;

        &:hover {
            text-decoration: underline;
        }
    }

    // .footer-list__icon

    &__icon {
        fill:$color-primary;
        margin-right: 20px;
    }

    // .footer-list__text

    // &__text {}
}