.start-screen {
    background: $start-screen-gradient-background;
    height: 100vh;

    // .start-screen__inner

    &__inner {
        height: 100%;
        position: relative;
        display: flex;
        align-items: center;
    }

    // .start-screen__puppy

    &__puppy {
        position: absolute;
        right: 40px;
        bottom: 0;
        z-index: 0;
        height: 80%;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    // .start-screen__content

    &__content {
        padding-top: 70px;
        z-index: 1;
    }
}

.start-screen-content {
    max-width: 39%;

    // .start-screen-content__title

    &__title {
        color: $color-light-xl;
        margin-bottom: 42px;
    }

    // .start-screen-content__text

    &__text {
        color: $color-dark-s;
        margin-bottom: 42px;
    }

    // .start-screen-content__button

    // &__button {}
}



