.addition {
    
    padding-top:80px;
    padding-bottom: 87px;
    background-color: $color-light-l;

    display: grid;
    align-items: center;
    grid-template-columns: auto auto;

    // .addition__image

    &__image {
        max-width: 505px;
        margin-right: 30px;
        justify-self: end;
    }

    // .addition__content

    &__content {
        max-width: 380px;
    }

    // .addition__title

    &__title {
        margin: 0;
        margin-bottom: 20px;
        color:$color-dark-l;
    }

    // .addition__description

    &__description {
        margin-bottom: 19px;
        color:$color-dark-l;
    }

    // .addition__donate-button

    &__donate-button {
        margin-bottom: 9px;
    }

    // .addition__text

    &__text {
        color:$color-dark-m;
        font-family: 'Arial';
        font-style: italic;
        font-weight: 400;
        font-size: 12px;
        line-height: 18px;
    }
}


.donate-button {
    background-color: $color-primary;
    border-radius: 9px;
    padding: 10px 15px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    text-decoration: none;
    color:$color-dark-l;

    &:hover {
        background-color: $color-primary-light;
    }

    // .donate-button__icon

    &__icon {
        width: 30px;
        height: 23px;
        fill: $color-dark-3xl;
        margin-right: 15px;
    }

    // .donate-button__telephone

    &__telephone {
        margin: 0;
        color:$color-dark-l;
    }
}