.help {
    padding-top:80px;
    padding-bottom: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;

    // .help__title

    &__title {
        text-align: center;
        margin-top: 0;
        margin-bottom: 60px;
        color:$color-dark-l;
    }

    // .help__list

    &__list {
        padding: 0;
        margin: 0;
        list-style-type: none;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
    }

    // .help__item

    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-left:5%;
        margin-right:5%;
        margin-bottom: 25px;
    }

    // .help__icon

    &__icon {
        width: 60px;
        height: 60px;
        fill:$color-primary;
    }

    // .help__item-name

    &__item-name {
        margin-top: 30px;
        margin-bottom: 30px;
        color:$color-dark-l;
    }
}